.stream-demo {
  width: 100%;
  padding: 32px 16px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  .producers {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .add {
      padding: 6px 14px;
      cursor: pointer;
      border: 2px solid;
      border-radius: 4px;
      margin-bottom: 12px;
    }
    .producer {
      cursor: pointer;
      position: relative;
      margin: 12px 0px;
      .name {
        border: 2px solid #d1d5dbad;
        border-radius: 2px;
        padding: 8px;
        position: absolute;
        background-color: #d1d5dbad;
      }
      input {
        flex: 1;
        padding: 8px;
        outline: none;
        padding-left: 42px;
      }
    }
  }
  .storage {
    width: 40%;
    height: 150px;
    border: 2px solid;
    border-radius: 4px;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    .data {
      width: fit-content;
      height: fit-content;
      transform: rotate(90deg);
    }
  }

  .consumers {
    display: flex;
    flex-direction: column;
    height: 500px;
    justify-content: center;
    .consumer {
      width: 100px;
      height: 100px;
      cursor: pointer;
      // display: flex;
      // flex-direction: column;
      .name {
        border: 2px solid;
        border-radius: 4px;
        padding: 12px;
        background-color: #57de5b;
        &:hover {
          background-color: #d1d5dbad;
        }
        &.busy {
          background-color: #da1d1d;
        }
      }
      .data {
        margin-top: 5px;
      }
    }

  }
}
